body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: darkcyan no-repeat fixed;
  background-image: linear-gradient(to right, darkcyan, rgb(156, 233, 233));
}


.home-container h1{
  
  text-align: center;
  font-family:cursive;
  margin-top: 5%;
}


.home-container input{
  margin:auto;
  display:block;
  width: 65%;
  height:30px;
  border: solid 1px;
  border-radius:5px;
  font-family: cursive;
}

.btn-submit{

  margin:auto;
  display:block;
  background-color: black;
  color:white;
  margin-top:2%;
  padding-left:15px;
  padding-right:15px;
  padding-top:6px;
  padding-bottom:6px;
  border: solid black 1px;
  border-radius:5px;
  cursor: pointer;
  font-size:medium;
  font-family: cursive;
  margin-bottom: 2%;
}

.home-container button:hover{

  border:solid white 1px;
}

#outputURL{
  background-color: whitesmoke;
  font-family: cursive;
  
}

.error-msg{
  font-size:medium;
  color: red;
  font-weight:bold;
  text-align: center;
  font-family: cursive;
}

.btn-combo{

  margin:auto;
  margin-left: 46%;
  margin-top: 2%;
  
}

.btn-combo button{
  background-color: black;
  color:white;
  margin-top:2%;
  padding-left:15px;
  padding-right:15px;
  padding-top:6px;
  padding-bottom:6px;
  border: solid black 1px;
  border-radius:5px;
  cursor: pointer;
  font-size:medium;
  font-family: cursive;
  margin-left: 2px;
}



/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: white;
  color: black;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  font-size:smaller;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.footer{

  font-size:smaller;
  text-align: center;
  color: whitesmoke;
  font-family: cursive;
  margin-top:2%;
  
}

.footer a{
  text-decoration:none;
  color:white;
  font-family: cursive;
}

.copy-message{
  color:black;
  font-family: cursive;
  background-color:white;
  padding: 5px 0;
  text-align: center;
  width:4%;
  margin:auto;
  display:block;
  margin-top:5px;
  font-size: smaller;
  border: solid 1px white;
  border-radius:5px;
}